.hero {
  margin-left: 0;
  text-align: center;
  background-color: rgba(229,115,115, 1);
  background-image: linear-gradient(#ef5350, #fff 40%, #fff);
  background-repeat: no-repeat;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFEF5350', endColorstr='#FFFFFFFF', GradientType=0); }
  .hero__image {
    position: relative;
    background-attachment: scroll;
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 60vh;
    background-image: url("/images/logo@1024.png"); }
    .hero__image::after {
      bottom: 0;
      content: "";
      left: 0;
      position: absolute;
      right: 0;
      top: 0; }

@media only screen and (max-width: 480px) {
  .hero__image {
    background-image: url("/images/logo@480.png"); } }
